










export default {
  name: "Loading",
  props: {
    status: Array,
    completion: Number,
    tasks: Number,
  },
  computed: {
    Status(): string {
      const statusList: Array<any> = this.status as Array<any>
      const status = statusList.find((e: any) => {
        return e.id === this.completion
      })
      return `[${this.completion}/${this.tasks}] ${status.text}`
    },
    Completion(): number {
      return 100 * (this.completion / this.tasks)
    },
  },
}
